<template>
  <div class="custom-panel" :style="style">
    <div class="p-content" :class="contentClass">
      <slot></slot>
    </div>
    <!--top-->
    <div class="p-borders-list">
      <border v-if="pivot.includes('bottom')"
        class="panel-border h"
        :height="bordersHeight | px"
        :width="pWidth | px"
        :top="(-bordersHeight/2) | px"
        @dragstart="()=>{ dragstart('top') }"
        @dragend="dragend">
      </border>
      <!--bottom-->
      <border v-if="pivot.includes('top')"
        class="panel-border h" 
        :height="bordersHeight | px" 
        :width="pWidth | px" 
        :top="(pHeight-bordersHeight/2) | px"
        @dragstart="()=>{ dragstart('bottom') }"
        @dragend="dragend">
      </border>
      <!--left-->
      <border v-if="pivot.includes('right')"
        class="panel-border v"
        :height="pHeight | px" 
        :width="bordersHeight | px" 
        :left="(-bordersHeight/2) | px" 
        @dragstart="()=>{ dragstart('left') }"
        @dragend="dragend">
      </border>
      <!--right-->
      <border v-if="pivot.includes('left')"
        class="panel-border v"
        :height="pHeight | px" 
        :width="bordersHeight | px"
        :left="(pWidth-bordersHeight/2) | px"
        @dragstart="()=>{ dragstart('right') }"
        @dragend="dragend">
      </border>
    </div>
    <v-style type="text/css" v-if="curBorder">
      body *{
        cursor: {{this.sides.get(curBorder)=='h'?'ew-resize':'ns-resize'}} !important;
      }
    </v-style>
  </div>
</template>

<script>
import border from './border.vue'
export default {
  components: { border },
  name:'panel',
  props:{
    pivot:{
      type:Array,
      default(){
        return ['top','left','bottom','right'];
      }
    },
    bordersHeight:{
      type:Number,
      default:10
    },
    magnitHeight:{
      type:Number,
      default:30
    },
    width:{
      type:Number,
      default(){
        return -1;
      }
    },
    height:{
      type:Number,
      default(){
        return -1;
      }
    },
    maxWidth:{
      type:Number,
      default(){
        return -1;//-1 - none
      }
    },
    minWidth:{
      type:Number,
      default(){
        return -1;//-1 - none
      }
    },
    maxHeight:{
      type:Number,
      default(){
        return -1;//-1 - none
      }
    },
    minHeight:{
      type:Number,
      default(){
        return -1;//-1 - none
      }
    },
    contentClass:{
      type:String,
      default:""
    }
  },
  watch:{
    width:function(){
      this.pWidth = this.width;
    },
    height:function(){
      this.pHeight = this.height;
    },
    pivot:function(){
      if(this.pivot.length < 1 || this.pivot.length > 2){
        console.error("pivot consists of 1/2 items: top/bottom and left/right [pivot=\""+this.pivot+"\"]");
      }
      if(this.sides.get(this.pivot[0]) == this.sides.get(this.pivot[1]))
        console.error("values of pivot must have diferent sides");
    },
    maxWidth:function(){ this.validateWidth() },
    minWidth:function(){ this.validateWidth() },
    maxHeight:function(){ this.validateHeight() },
    minHeight:function(){ this.validateHeight() }

  },
  data(){
    return{
      pWidth:100,
      pHeight:100,
      zIndex:99999,
      curBorder:null,
      magnit:false,
      sides:new Map([
        ["top","v"],
        ["bottom","v"],
        ["left","h"],
        ["right","h"]
      ])
    }
  },
  mounted(){
    this.validateWidth();
    this.validateHeight();
    this.$emit("resize",{ side:this.curBorder, width:this.pWidth, height:this.pHeight });
  },
  filters:{
    px:function px(value){
      return value+"px";
    }
  },
  methods:{
    dragstart(side){
      this.curBorder = side;
      window.addEventListener('mousemove',this.ondrag);
    },
    ondrag(e){
      let next = 0;
      let borderOffset = this.bordersHeight/2;
      if(this.sides.get(this.curBorder)=='v'){
        next = this.curBorder == 'top'?
        (this.$el.getBoundingClientRect().bottom-e.clientY):
        (e.clientY-this.$el.offsetTop);
        // console.log(this.next,this.magnitHeight)
        if(next<=this.magnitHeight)
          next = 0;
        if((this.minHeight==-1 || next >= this.minHeight) && (this.maxHeight==-1 || next <= this.maxHeight)){
          this.pHeight = next;
          this.$emit("resize",{ side:this.curBorder, width:this.pWidth, height:this.pHeight });
          this.l_emit = false;
        }
        else if(!this.l_emit){
          this.$emit("resize",{ side:this.curBorder, width:this.pWidth, height:this.pHeight });
          this.l_emit = true;
        }
      }
      else{
        next = this.curBorder == 'left'
        ?(this.$el.getBoundingClientRect().right-e.clientX)
        :(e.clientX-this.$el.offsetLeft);
        // console.log(this.next,this.magnitHeight)
        if(next<=this.magnitHeight)
          next = 0;
        if((this.minWidth==-1 || next >= this.minWidth) && (this.maxWidth==-1 || next <= this.maxWidth)){
          this.pWidth = next;
          this.$emit("resize",{ side:this.curBorder, width:this.pWidth, height:this.pHeight });
          this.l_emit = false;
        }
        else if(!this.l_emit){
          this.$emit("resize",{ side:this.curBorder, width:this.pWidth, height:this.pHeight });
          this.l_emit = true;
        }
      }
    },
    dragend(e){
      window.removeEventListener('mousemove',this.ondrag);
      this.curBorder = null;
    },
    validateWidth(){
      let isMin = this.minWidth!=-1;
      let isWidth = this.width!=-1;
      if(isMin)
        this.pWidth = this.minWidth;
      if(isWidth)
        this.pWidth = this.width;
    },
    validateHeight(){
      let isMin = this.minHeight!=-1;
      let isHeigh = this.height!=-1;
      if(isMin)
        this.pHeight = this.minHeight;
      if(isHeigh)
        this.pHeight = this.height;
    }
  },
  computed:{
    style(){
      return{
        '--pl-width':this.pWidth + "px",
        '--pl-height':this.pHeight + "px",
        '--pl-z-index':this.zIndex
      }
    }
  }
}
</script>

<style lang="scss">
.p-content{
  width: 100%;
  min-height: 100%;
  height: max-content;
}
.p-borders-list, .magnit-zones{
  position: absolute;
}
.custom-panel {
  display: flex;
  width: var(--pl-width);
  height: var(--pl-height);
  min-width: var(--pl-width);
  min-height: var(--pl-height);
  z-index: var(--pl-z-index);
}
.panel-border {
  border: 1px inset transparent;
  background: rgba(0, 0, 0, 0);
  transition: 0.1s ease-in-out;
  transition-property: background-color, border;
  z-index: 10;
  &:active {
    border: 4px inset #00000059;
    background: #dcdcdc;
    border-radius: 3px;
  }
  &.h {
    cursor: ns-resize;
  }
  &.v {
    cursor: ew-resize;
  }
}
.magnit-zones{
  &.p-magnit{
    background: rgba(255, 192, 203, 0.658);
  }
}
</style>