import OrderItem from '../orderItem.js';
import Order from '../order';
export default {
	data(){
		return{
      state:0,
      states:{
        error:-1,
        loading:0,
        loaded:1
      },
			sots:[],
			zones:[],
			waves:[],
			availableWaves:[],
			filterWaves:[],
			orders:[],
			payments:[],
			statuses:[],
      phonePrefixes:[],
      mode:0,
      modes:{
        default:-1,
        edit:0,
        correct:1,
        applyCorrect:2
      },
		}
	},
	mounted(){
    this.getWaves().then(waves=>this.waves=waves),
		this.getWaves(true).then(waves=>this.availableWaves=waves),
		this.getWaves(false,true).then(waves=>this.filterWaves = waves);
    this.apiGetPefixes().then((prefixes)=>{
      this.phonePrefixes = prefixes;
      this.phonePrefixes.forEach((prefix)=>{
        try{ prefix.images = JSON.parse(prefix.images); }
        catch{ prefix.images = []; }
      });
    })
    .then(()=>this.getOrders())
    .then(()=>this.state = this.states.loaded)
    .catch(()=>this.state = this.states.error);
	},
	methods:{
    apiGetPefixes(){
      return axios.get("/Api/getPhonePrefixes").then(({data})=>data.phonePrefixes)
    },
    getOrders(filters={},params={}){
      if(this.getOrdersRequest) this.getOrdersRequest.cancel("repeated request.");
      const CancelToken = axios.CancelToken;
      this.getOrdersRequest = CancelToken.source();
      filters = Object.fromEntries(Object.entries(filters).partmap((entrie)=>{//у фильтров отбрасываем поля с нулевыми значениями, чтобы они не перезаписывали существующие
        return entrie[1]?entrie:undefined;
      }));
      const api = '/Api/getOrders';
      return axios
      .get(api, {
        headers: {'X-Access-Token': this.api_token},
        params:{
          'phone':this.phone || undefined,
          'dFrom': this.d_from,
          'dTo': this.d_to,
          'status': this.status,
          'nopay':this.searchParams?.get("nopay") || undefined,
          'orderstimeout':this.searchParams?.get("orderstimeout") || undefined,
          'orderId':this.searchParams?.get("orderId") || undefined,
          ...filters,
          ...params
        },
        cancelToken: this.getOrdersRequest.token
      })
      .then(response => {
        Promise.all([
          this.getWaves().then(waves=>this.waves=waves),
          this.getWaves(true).then(waves=>this.availableWaves=waves),
        ])
        .then(()=>{
          this.orders = [];
          response.data.orders.forEach((order)=>{
            this.orders.push(new Order().set(
              {
                ...order,
                items: [],
                itemsVisible: false,
                phonePrefix: this.getPrefix(order.phone),
                phone: order.phone ? order.phone.replace(this.getPrefix(order.phone),'') : '',
                phoneConsigneePrefix: this.getPrefix(order.phoneConsignee),
                phoneConsignee: order.phoneConsignee ? order.phoneConsignee.replace(this.getPrefix(order.phoneConsignee),'') : '',
                deliveryDate: new Date(order.deliveryDate).toShortDateString(),  
                waveId: order.waveId || this.waves[0].id,
                actions: true
              }
            ));
          })
          //this.orders = response.data.orders;
          this.statuses = response.data.statuses;
          this.payments = response.data.payments;
        })
      })
      .catch(error => {
        if (axios.isCancel(error)) {
          console.log(`Request canceled (${api}): ${error.message}`);
        } else {
          window.etemp = error;
          console.error(error);
          this.errored = true;
          this.alertRequestError(error);
          window.location.reload();
        }
      })
    },
    getPrefix(phone){
      if(!phone) return '+38';
      if(phone[0]=='+') return phone[1]=='7'?'+7':'+38'
      let index = phone.indexOf("+");
      if(index<0) return "+38";
      else return phone.slice(0,index);
    },
    getWaves(onlyAvailable = undefined,groupByZone=false){
      return new Promise((resolve,reject)=>{
        axios
        .get("/Api/getTimeWaves"+(groupByZone?"Group":""),{ params:{ t:onlyAvailable } })
        .then(({data})=>{
          resolve(data.modify((item,index,set)=>{ set({ ...item, value:item.timeFrom.split(":",2).join(":")+" - "+item.timeTo.split(":",2).join(":"), disabled:Boolean(item.disabled) }); }))
        })
        .catch((e)=>{ reject(e); console.error(e); })
      })
    },
    updateItems(order){
      return axios.get("/Api/getOrderItems", { headers:{ "X-Access-Token":this.api_token }, params:{ orderId:order.id } })
        .then(({ data:items })=>{
          order.items = items.map(item=>new OrderItem(item));
        })
        .catch(console.error);
    },
    /**
     * 
     * @param {Order} order 
     * @param {Number} mode 
     * @returns undefined
     */
    openOrder(order, mode){
      let modes = Object.values(this.modes);
      let modeExists = mode in modes;
      this.mode = modeExists?mode:(this.modes.default || modes[0]);
      if(!modeExists) console.warn(`Mode ${mode} does not exists.`);
      return new Promise((resolve)=>{
        let edit = ()=>this.editableOrder = order;
        if(order.id > 0 && !order.items.length)
          this.updateItems(order).then(edit).then(resolve);
        else { edit();
          resolve();
        };
      })
    },
    filterOrders(filters={}){
      this.orders = [];
      this.state = this.states.loadign;
      this.getOrders(filters)
      .then(()=>this.state = this.states.loaded)
      .catch(()=>this.state = this.states.error);
    },
    closeOrder(){
      this.editableOrder = null;
    }
	}
}