<template>
  <div class="orders">
    <orders-view 
      v-if="!editableOrder"
      :orders="orders"
      :api_token="api_token"
      :phone="phone"
      :dateRange="[d_from,d_to]"
      :stat="status"
      :shop_url="shop_url"
      :waves="waves"
      :availableWaves="availableWaves"
      :filterWaves="filterWaves"
      :payments="payments"
      :statuses="statuses"
      :state="state"
      :ordersWithCorrects="Object.keys(ordersInfo.corrects)"
      :showCorrects="showCorrects"
      :showDelivery="showCorrects" 
      @order-created="orders.unshift($event); openOrder($event, modes.edit);"
      @refuse="refuseOrder"
      @requestItems="updateItems"
      @filter="filterOrders">
      <template #actions="{ order }">
        <async-button v-if="[1, 7].includes(order.status)" :class="['btn edit',{ locked:ordersInfo.locked.includes(order.id) }]" @click="tryTakeOrder(order)">
          {{ordersInfo.locked.includes(order.id)?'Заблокировано':'Изменить'}}
        </async-button>
        <async-button v-if="showCorrects && [7, 2, 5].includes(order.status)" :class="['btn',{ isCorrects:order.id in ordersInfo.corrects }]" @click="correct(order)">
          Корректировки
        </async-button>
      </template>
      <template #buttons>
        <button v-if="canUnlock"
          :disabled="!ordersInfo.locked.length"
          class="unlock"
          @click="unlocking = true; freeOrder().finally(()=>unlocking=false)">
          <circle-loading v-if="unlocking" style="position: absolute; left: 50%; transform: translateX(-50%);" :radius="15" :ringWeight="10"></circle-loading>
          <span v-visible="!unlocking">Разблокировать все заказы</span>
        </button>
      </template>
    </orders-view>
    <order-edit-view ref="editView"
      v-if="editableOrder && mode==modes.edit"
      :order="editableOrder"
      :waves="waves"
      :availableWaves="availableWaves"
      :sots="sots"
      :zones="zones"
      :api_token="api_token"
      :payments="payments"
      :statuses="statuses"
      :lgots="lgots"
      :shop_url="shop_url"
      :error-rate="errorRate"
      :discountMethodsTypes="discountMethodsTypes"
      :phonePrefixes="phonePrefixes"
      @cancel="onCancelEdit"
      @save="saveOrder">
    </order-edit-view>
    <create-corrections-panel
      v-if="editableOrder && mode==modes.correct"
      :order="editableOrder"
      :discountMethodsTypes="discountMethodsTypes"
      :shop_url="shop_url"
      :sots="sots"
      :zones="zones"
      :api_token="api_token"
      :errorRate="errorRate"
      :waves="waves"
      @cancel="onCancelEdit">
    </create-corrections-panel>
    <apply-corrections-panel 
      v-if="editableOrder && mode==modes.applyCorrect"
      :order="editableOrder"
      :discountMethodsTypes="discountMethodsTypes"
      :shop_url="shop_url"
      :sots="sots"
      :zones="zones"
      :api_token="api_token"
      :errorRate="errorRate"
      :waves="waves"
      @cancel="onCancelEdit">
    </apply-corrections-panel>
  </div>
</template>

<script>
import OrdersView from "./view/orders-view.vue";
import OrderEditView from "./editor/panels/edit/order-edit-view.vue";
import Order from './order';
import OrdersMixin from './mixins/orders.js';
import CircleLoading from '../../mini/circle-loading.vue';
import CreateCorrectionsPanel from './editor/panels/corrections/create-corrections-panel/create-corrections-panel.vue';
import ApplyCorrectionsPanel from './editor/panels/corrections/apply-corrections-panel/apply-corrections-panel.vue';

export default {
  name:"orders",
  mixins:[OrdersMixin],
  components:{ OrdersView, OrderEditView, CircleLoading, CreateCorrectionsPanel, ApplyCorrectionsPanel },
  props:{
    api_token:String,
    phone:String,
    d_from:String,
    d_to:String,
    status:[Number,String],
    shop_url:String,
    "error-rate":Number,
    "show-pickup":Boolean,
    canUnlock:Boolean,
    showCorrects:Boolean
  },
  data(){
    return{
      editableOrder:null,
      lgots:[{id:0, title:'нет'}, {id:1, title:'пенсионер'}],
      discountMethodsTypes:{
        dontUse:-1,
        coupon:0,
        promocode:1,
        bonus:2
      },
      editLoading:-1,
      unlocking:false,
      ordersInfo:{
        locked:[],
        corrects:{},
      },
      searchParams:new URL(document.URL).searchParams,
    }
  },
  mounted(){
    this.getPolygons({ sotId:-1, disabled:0, zoneDisable:1 }).then(({zones,sots})=>{ this.zones = zones; this.sots = sots });
    window.addEventListener('getAlerts',({ detail:{ locks, corrects } })=>{ this.ordersInfo = { locked:locks||[], corrects:corrects||{} } });
    window.addEventListener('beforeunload',()=>{
      if(this.editableOrder) this.freeOrder(this.editableOrder.id);
    })
  },
  methods:{
    getPolygons(params){
			return new Promise((resolve,reject)=>{
				axios
					.get("/Api/getPoligons",{ params })
					.then(({data})=>{
						let getPolygons = (type,polys)=>{
							return polys.map((poly)=>{ return {
								geometry:JSON.parse(poly.geometry),
								options:{
									description:poly.description || ((type=='sot'?"Сота ":"Зона ")+poly.id),
									fillColor:poly.fillColor|| "#1A78EE",
									fillOpacity:poly.fillOpacity || (type=='sot'?0:0.6),
									strokeColor:poly.strokeColor || "#1A78EE",
									strokeOpacity:(type=='sot'?"0":(poly.strokeOpacity || "0")),
									strokeWidth: 3
								},
								properties:{
									id:poly.id,
									type,
								},
                conditions:{
                  cost:poly.cost,
                  limit:poly.limit,
                  limit_lgot:poly.limit_lgot,
                  limit_min:poly.limit_min,
                  balloon:poly.balloon,
                  description:poly.description,
                }
							}});
						}
						resolve({	zones:getPolygons('zone',data.zones),	sots:data.sots?getPolygons('sot',data.sots):null	}, data);
					})
					.catch((e)=>{ console.error(e); reject(e) });
			})
		},
    setOrder(api_token,order){
      return new Promise((resolve,reject)=>{
        if(!api_token) reject(new Error("(setOrder) Не передан api_token"));
        if(!order) reject(new Error("(setOrder) Не передан order"));
        axios
        .post('/Api/setOrder', {
          headers: {'X-Access-Token': api_token, "content-type": "application/json"},
          params: {
            order:{
              ...order,
              phone:order.phonePrefix+order.phone,
              phoneConsignee:order.phoneConsigneePrefix+order.phoneConsignee
            },
          },
        },
        {headers: {'X-Access-Token': api_token, "content-type": "application/json"}})
        .then(response => {
          if(!response.data.error && response.data.code==200){
            if(order.id==0)
              order.id = response.data.order.id;
            resolve(response);
          }
          else {
            reject({ type:"thrown", response });
          }
        })
        .catch(error => {
          reject({ type:"catched", response:error });
        })
      })
    },
    saveOrder(order,resolve,reject){
      axios.defaults.headers.common["X-Access-Token"] = this.api_token;
      this.setOrder(this.api_token, order)
        .then(()=>this.freeOrder(order.id))
        .then(()=>resolve?.())
        .catch(error=>reject?.(error));
    },
    refuseOrder(order){//обработка клика кнопки отмены заказа
      if(!confirm("Подтвердите отмену заказа №"+order.id+" !"))
        return;
      order.status = 3;
      this.saveOrder(order);
      this.freeOrder(order.id);

     // window.location.reload();
    },
    onCancelEdit(order,draft){
      if(draft){
        let index = this.orders.indexOf(order);
        this.orders[index] = new Order().set(draft);
      }
      this.closeOrder();
      this.freeOrder(order.id);
    },
    tryTakeOrder(order){
      if(this.editLoading>=0) return;
      this.editLoading = order.id;
      return axios.get("/checkOrderLock",{ params:{ orderId:order.id , edit:1 } })//заблокировать зказ
        .then(({ data })=>{
          if(data.lock.length) {
            let output = "Заказ уже редактируется:\n";
            data.lock.forEach(moderator => {
              output +=  `Имя редактирующего: ${moderator.name} (id: ${moderator.id})\n`
            });
            alert(output);
          }
          else this.openOrder(order, this.modes.edit);
        }).catch((error)=>{
          console.log(error);
          alert("Ошибка: "+error.code);
        }).finally(()=>{
          this.editLoading = -1;
        });
    },
    freeOrder(orderId = -1){
      return axios.get("/ordersUnlock",{ params:{ orderId } })//разблокировать зказ
        .then(({ data })=>{
          if(!data.success){
            console.error(data);
            return;
          }
          if(orderId>=0) this.ordersInfo.locked.remove(orderId);
          else this.ordersInfo.locked = [];
        }).catch((error)=>{
          console.log(error);
          alert("Ошибка: "+error);
        })
    },
    /**
     * @param {Order} order Заказ
     * @description Открывает заказ в режиме корректировки
     * TODO: replace /Api/getAlerts
     */
    async correct(order){
      let { data:{ corrects } } = await axios.get("/Api/getAlerts",{ headers:{ 'X-Access-Token': this.api_token } })
      this.openOrder(order, order.id in corrects?this.modes.applyCorrect:this.modes.correct);
    },
  }
}
</script>

<style lang="scss">
.orders{
  .groupbox-actions{
    display: flex;
    flex-direction: column;
    .btn{
      white-space: initial;
      margin: 3px 0;
    }
  }
}
.btn{
  &.edit{
    position:relative;
    &.editLoading{
      background: #afafaf;
    }
    &.locked{
      background: #ffa8a8;
    }
  }
  &.isCorrects{
    background: #ffc55a;
  }
}
button.unlock{
  position: relative;
}
</style>