<template>
  <transition name="modal-fade" v-if="isOpened">
    <div class="modal-backdrop">
      <div class="modal-age"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
      >
        <section
          class="modal-age_body"
          id="modalDescription"
        >
          <slot name="body">
            I'm the default body!
          </slot>
        </section>
        <footer class="modal-age_footer">
          <slot name="footer">
            <button
              type="button"
              class="btn-agree"
              @click="confirm"
              aria-label="Close modal"
            >
              {{ok}}
            </button>
            &nbsp;
            <input
              type="submit"
              class="btn-agree n-agree"
              aria-label="Close modal"
              :value="cancel"
            />
          </slot>
        </footer>
      </div>
    </div>
  </transition>
</template>
<script>
  export default {
    name: 'modal_age',
    props:{
      ok:{
        type:String,
        default(){
          return 'ДА'
        }
      },
      cancel:{
        type:String,
        default(){
          return 'НЕТ'
        }
      },
      group_id:{
        type:Number,
        default(){
          return 0
        }
      },
      pickup:{
        type:Number,
        default(){
          return 0
        }
      }
    },
    data() {
      let isOpened = false;
      if(this.group_id==10 || this.group_id==14 || this.pickup>0){
        if(localStorage.mtAge && (Date.now()-localStorage.mtAge)/60000 < 60){
          isOpened=false;
          localStorage.mtAge=Date.now();
        }else{
          isOpened=true;
        }
       
      }
      return {
        choose: '',
        isOpened: isOpened,
        mtAge: 0
      };
    },
    mounted(){
      
    },
    methods: {
      close() {
        this.$emit('close');
        this.isOpened=false
      },
      confirm() {
        localStorage.mtAge = Date.now();
        this.$emit('confirm');
        this.$emit('close');
        this.isOpened=false;
      },
    },
  };
</script>
