<template>
	<div class="waves-report">
		<label>c:</label>
		<input type="date" v-model="dFrom" />
		<label>по:</label>
		<input type="date" v-model="dTo"/>
		<input type="button" value="Применить" @click="update()">
		<br>
		<table v-if="!updating && report.length>0" class="report-table">
			<thead>
				<td>ID</td>
				<td>Период</td>
				<td>Количество заказов</td>
				<td></td>
			</thead>
			<tbody>
				<tr v-for="wave in report" :key="wave.id" class="wave-row">
					<td>{{wave.id}}</td>
					<td>{{wave.value}}</td>
					<td>{{wave.count}}</td>
					<td><input class="action" type="button" value="Фильтровать заказы" :title="'Фильтровать заказы по волне'+wave.value" @click="$emit('select',wave)"/></td>
				</tr>
			</tbody>
		</table>
		<span v-if="!updating && !report.length" style="color:#931515; font-weight:bold;">В выбранном периоде отсутствуют заказы или у этих заказов не выбрана волна.</span>
		<circle-loading v-if="updating" :radius="41" :ringWeight="14"></circle-loading>
	</div>
</template>

<script>
import circleLoading from '../../../mini/circle-loading.vue'
export default {
  components: { circleLoading },
	props:{
		waves:Array,
		defaultFrom:{
			type:String,
			default:new Date().toISOString().split("T")[0]
		},
		defaultTo:{
			type:String,
			default:new Date().toISOString().split("T")[0]
		},
		api_token:{
			type:String,
			required:true
		}
	},
	data(){
		return{
			report:[],
			updating:false,
			dFrom:this.defaultFrom,
			dTo:this.defaultTo
		}
	},
	methods:{
		getWavesReport(d1,d2,waves){
      return new Promise((resolve,reject)=>{ 
        axios.get("/Api/getWavesReport", { headers:{ "X-Access-Token":this.api_token }, params:{ d1, d2, wId:waves.map(wave=>wave.id).join(",") }})
          .then(({data})=>{
			if(data.error) { console.error("GET "+window.location.origin+"/Api/getWavesReport "+data.error); return; };
            let report = [];
			this.rwaves = data.waves;
            data.waves.forEach((item)=>{ report.push({...waves.find((w)=>item.wId.split(",").includes(String(w.id))), id:item.wId, count:item.count }); });
            resolve(report);
          }).catch((e)=>{ reject(e); console.error(e); })
      })
    },
		update(){
			this.updating = true;
			return this.getWavesReport(this.dFrom,this.dTo,this.waves).then((report)=>{ this.report = report;}).finally(()=>{ this.updating = false });
		}
	}
}
</script>

<style lang="scss">
.report-table {
	width: 100%;
	td{
		border: 1px solid;
	}
	thead{
		font-weight: bold;
	}
	.wave-row{
		.action{
			visibility: hidden;
		}
		&:hover{
			box-shadow: -4px 0px 0px 0px grey;
    	background: #8d8d8d2e !important;
			.action{
				visibility:visible;
			}
		}
	}
}
.waves-report {
	min-height: 350px;
	.circle-loading {
		margin: 150px auto;
		.loader{
			border-color: rgb(140 140 140 / 20%);
			border-left-color: #931515;
		}
	}
}
</style>